import React from "react";

import ReactHtmlParser from 'react-html-parser';

import ShowXlTableArrayOfArrays from "../mediatemplates/ShowXlTableArrayOfArrays";
import { GetReadyProcessBalanceStackedBars } from "../mediatemplates/PrepareDataFromDictionaryForChart";
import ShowXlTableWithPredefinedIndicators from "../mediatemplates/ShowXlTableWithPredefinedIndicators";
import ShowMedia, { GetSeriesCategoriesOptionsForApexChart, GetReadyScatterApexChart, GetReadyDatalineQuotesApexChart } from "../mediatemplates/ShowMedia";
import MediaTemplatesAccountingWithProfitsCash from "../mediatemplates/MediaTemplatesAccountingWithProfitsCash";

import { createProtoArray } from "../features/spreadsheet/spreadsheetSlice";
import { useFirebaseNode } from "../hooks/useFirebaseNode";
import Avatar from 'avataaars'

import Alert from 'react-bootstrap/Alert'
import Card from 'react-bootstrap/Card'



import Layout from "../components/layout"

function topType() {
    let variants = ["NoHair", "Eyepatch", "Hat", "Hijab", "Turban", "WinterHat1", "WinterHat2", "WinterHat3", "WinterHat4", "LongHairBigHair", "LongHairBob", "LongHairBun", "LongHairCurly", "LongHairCurvy", "LongHairDreads", "LongHairFrida", "LongHairFro", "LongHairFroBand", "LongHairNotTooLong", "LongHairShavedSides", "LongHairMiaWallace", "LongHairStraight", "LongHairStraight2", "LongHairStraightStrand", "ShortHairDreads01", "ShortHairDreads02", "ShortHairFrizzle", "ShortHairShaggyMullet", "ShortHairShortCurly", "ShortHairShortFlat", "ShortHairShortRound", "ShortHairShortWaved", "ShortHairSides", "ShortHairTheCaesar", "ShortHairTheCaesarSidePart"];
    return variants[Math.floor(Math.random() * variants.length)];
}

function clothType() {
    let variants = ["BlazerShirt", "BlazerSweater", "CollarSweater", "GraphicShirt", "Hoodie", "Overall", "ShirtCrewNeck", "ShirtScoopNeck", "ShirtVNeck"];
    return variants[Math.floor(Math.random() * variants.length)];
}

function clotheColor() {
    let variants = ["Black", "Blue01", "Blue02", "Blue03", "Gray01", "Gray02", "Heather", "PastelBlue", "PastelGreen", "PastelOrange", "PastelRed", "PastelYellow", "Pink", "Red", "White"];
    return variants[Math.floor(Math.random() * variants.length)];
}

function hairColor() {
    let variants = ["Auburn", "Black", "Blonde", "BlondeGolden", "Brown", "BrownDark", "Platinum", "Red"];
    return variants[Math.floor(Math.random() * variants.length)];
}

let currentDay = new Intl.DateTimeFormat("en", {
    weekday: "short",
    year: "numeric",
    month: "short",
    day: "numeric"
}).format(new Date()).replace(/[^a-zA-Z0-9]/g, "_");

export default function CurrentLaboratory() {
    // const { data: usersProjects, loading, error } = useFirebaseNode('currentLaboratoryDay/' + currentDay + '/cases/');
    const { data: usersProjects, loading, error } = useFirebaseNode("/currentDay/" + currentDay + '/cases/');


    if (!!loading || !usersProjects) { return <Layout><div>...</div></Layout> }
    if (!!error) { return <Layout>:-(</Layout> }

    let currentCases = Object.keys(usersProjects).map(item => { return usersProjects[item] });
    console.log(currentCases);

    currentCases.sort(function (a, b) {
        var timeA = !!a?.timestamp ? a.timestamp : 0;
        var timeB = !!b?.timestamp ? b.timestamp : 0;
        if (timeA > timeB) {
            return -1;
        }
        if (timeA < timeB) {
            return 1;
        }
        return 0;
    });

    const uniqueTitles = !!currentCases ? [...new Set(currentCases.map((item) => item.title))] : [];

  //  console.log(uniqueTitles);

    return <Layout>
        <div>     {!!uniqueTitles &&
            uniqueTitles.map((title) => (
                <div key={title}>
                    <Alert variant="primary">{title}</Alert>

                    {!!currentCases && currentCases.length > 0 && currentCases.filter(item => item.title === title).map(item =>
                        <Card key={item.id}>
                            <Card.Header>
                                {!!item.avatarUrl ? (
                                    <img
                                        src={item.avatarUrl}
                                        alt=""
                                        className="m-2"
                                        style={{
                                            verticalAlign: "middle",
                                            width: "50px",
                                            height: "50px",
                                            borderRadius: "50%",
                                            filter: "grayscale(100%)",
                                            objectFit: "cover",
                                        }}
                                    />
                                ) : null}
                                <small>{item.theme}</small>

                            </Card.Header>
                            <Card.Body>
                                {/* <Card.Title>{item.theme}</Card.Title> */}
                                <Card.Text>   <small>{item.itemString}</small></Card.Text>
                                {!!item?.answer && !(item.answer.toString().startsWith('-M')) ? <Card.Text>   <small>Ответ: {" "}{item.answer}</small></Card.Text> : null}
                                {!!item.comment.includes('__') ? null :  <Card.Text><small>{ReactHtmlParser(item.comment)}</small></Card.Text> }
                                 
                                {item.type === "spreadsheet" && !!item?.content && <ShowXlTableArrayOfArrays xlArray={createProtoArray(item.content, 0, 0)} />}
                                {item.type === "balancestackedbars" && <GetReadyProcessBalanceStackedBars quiz={item} />}

                                {(item.type === "datetimebarapexchart" || item.type === "datetimelineapexchart" ||
                                    item.type === "arraycategorieslineapexchart" || item.type === "arraycategoriesbarapexchart")
                                    && item?.deleted !== true && <GetSeriesCategoriesOptionsForApexChart firenode={item.content} mediaItem={item.type} />
                                }

                                {(item.type === "html" || item.type === "markdown") && <div>{ReactHtmlParser(item.content)}</div>}

                                {(item.type === "scatterquotesapexchart" || item.type === "scatterquotesapexchartoptions" ||
                                    item.type === "scatterapexchart") && item?.deleted !== true && <GetReadyScatterApexChart firenode={item.content} />
                                }

                                {(item.type === "datalinequotesapexchart" || item.type === "datalinequotesapexchartoptions" ||
                                    item.type === "datetimeareanocategoriesapexchart") && item?.deleted !== true && <GetReadyDatalineQuotesApexChart firenode={item.content} />
                                }


                                {item.type === "media" ? <ShowMedia datafirenode={"/usersCraft/" + item.email.replace(/[^a-zA-Z0-9]/g, "_") + "/data/" + item.content} /> : null}

                                {item.type === "accountingwithprofitscash" ? <MediaTemplatesAccountingWithProfitsCash records={item.content} mediaItems={item.mediaItems} /> : null}

                            </Card.Body>
                        </Card>
                    )}


                </div>
            ))}</div>

    </Layout>
}

